<template>
	<div>
		<div v-if="complete==1">
			<div class="top false">

			</div>
			<p style="font-size: 0.4rem;margin-top: 0.6rem;">
				很遗憾,通关失败!
			</p>
			<p style="font-size: 0.32rem;margin-top: 0.25rem;">
				答中{{per}}道题目,正确率{{right_prob}}
			</p>
			<button class="comfirm_btn" style="background: #fa8a12;" @click="goPer">再来一次</button>
			<!--<button class="comfirm_btn" style="background: #fa8a12;" @click="">查看答案</button>-->
			<button  class="comfirm_btn" style="color: #fa8a12;border:0.02rem solid #fa8a12 ;background: #FFFFFF;" @click="goHome">离开</button>
		</div>

		<div v-if="complete==2">
			<div class="top true">

			</div>
			<p style="font-size: 0.4rem;margin-top: 0.6rem;">
				恭喜您,通关成功!
			</p>
			<p style="font-size: 0.32rem;margin-top: 0.25rem;">
				答中50道题目,正确率100%
			</p>
			<button class="comfirm_btn" style="background: #fa8a12;">下一关</button>
			<button class="comfirm_btn" style="background: #fa8a12;">查看答案</button>
			<button class="comfirm_btn" style="color: #fa8a12;border:0.02rem solid #fa8a12 ;background: #FFFFFF;">离开</button>
		</div>
       
        <div v-if="complete==3">
        	<div class="top practice">
               <div>
               	<p style="font-size: 0.38rem;margin-top: 0.5rem;">正确率</p>
               	<p style="font-size: 0.44rem;margin-top: 0.1rem;">80%-90%</p>
               </div>
			</div>
			<p style="font-size: 0.3rem;margin-top: 0.6rem;">
				本次刷题正确率为80-90%,学霸您好!
			</p>
			<button class="comfirm_btn" style="background: #fa8a12;">继续刷题</button>
        	<button class="comfirm_btn" style="color: #fa8a12;border:0.02rem solid #fa8a12 ;background: #FFFFFF;">离开</button>
        </div>
        <div v-if="complete==4">
        	<div class="top test">
              
			</div>
			
			
			<p style="font-size: 0.4rem;margin-top: 0.6rem;">
				99分
			</p>
			<p style="font-size: 0.3rem;margin-top: 0.3rem;">
				用时 00:29:30
			</p>
			<button class="comfirm_btn" style="background: #fa8a12;">查看排名</button>
        	<!--<button style="color: #fa8a12;border:0.02rem solid #fa8a12 ;background: #FFFFFF;">离开</button>-->
        </div>
       
       
       
       
       
	
	</div>
</template>

<script>
	

	export default {
		
		created(){
			if(this.action==1){
				var num;
				num=Number(this.right_prob.split("%")[0])
				this.per=num*this.counted/100
				
			}
			

			
		},
		data() {
			return {
				complete: this.$route.query.Action,
				action:this.$route.query.Action,
				right_prob:this.$route.query.right_prob,
				counted:this.$route.query.counted,
				per:""
			}
		},
		methods:{
			goPer(){
				this.$router.go(-2)
			},
			goHome(){
				this.$router.go(-3)
			}
		}

	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	.top {
		width: 100%;
		height: 4.9rem;
		position: relative;
		
	}
	.false{
		background: url(../../assets/clear_false.png) no-repeat;
		background-size: 100% 100%;
	}
	.true {
		background: url(../../assets/clear_true.png) no-repeat;
		background-size: 100% 100%;
	}
	.test{
		
		background: url(../../assets/clear_test.png) no-repeat;
		background-size: 100% 100%;
	}
	
	.practice{
		background: url(../../assets/Practice.png) no-repeat;
		background-size: 100% 100%;
		div{
			position: absolute;
			width: 2.5rem;
			height: 2.5rem;
			background: #FFFFFF;
			text-align: center;
		
			
			border-radius:50% ;
			top: 19%;
			left: 33%;
			
			
			p{
				color: #fa8a12;
			}
		}
		
	}
	
	button{
		margin-bottom: 0.3rem;
	}
	
	
	
	
	
	
	p {
		text-align: center;
		color: #fa8a12;
	}
</style>


